@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.tabs{margin:15px auto;width:640px}.tabs__menu{margin-bottom:24px}.tabs__list{display:flex;justify-content:center;text-align:center}.tabs__item{position:relative;padding:15px 25px;margin-left:15px;top:0;width:150px;float:left;background-color:#181a1e;border:1px #ffffff26 solid;box-shadow:0 3px 10px #ffffff1a;transition:box-shadow .5s ease 0s, top .5s ease 0s, border .5s ease 0s, color .5s ease 0s}.tabs__item:first-child{margin-left:0}.tabs__item:hover{box-shadow:0 6px 15px #ffffff80;top:-3px;border:1px #ffffff4d solid;color:#fffc;cursor:pointer}.tabs__item--active{box-shadow:0 6px 15px #ff1e1e80;border:1px #ff1e1e66 solid;top:-3px;color:#ff3232}.tabs__item--active:hover{border-bottom:5px #ff2121 solid;box-shadow:0px 6px 15px #ff1e1e80;border:1px #ff1e1e66 solid;top:-3px;color:#ff3232}.tabs__text{text-align:justify}

